// /app/spa/page.tsx
'use client'

import { useEffect, useRef, useState } from 'react'
import HeroSection from '@/components/hero/HeroSection'
import styles from './spa.module.scss'

export default function SpaPage() {
  const [scrollEnabled, setScrollEnabled] = useState(false)

  const portfolioRef = useRef<HTMLDivElement>(null)
  const resumeRef = useRef<HTMLDivElement>(null)
  const contactRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!scrollEnabled) {
      document.body.classList.add('no-scroll')
    } else {
      document.body.classList.remove('no-scroll')
    }
  }, [scrollEnabled])

  const handleNavClick = (ref: React.RefObject<HTMLDivElement>) => {
    setScrollEnabled(true)
    setTimeout(() => {
      ref.current?.scrollIntoView({ behavior: 'smooth' })
    }, 100)
  }

  return (
    <main className={styles.pageWrapper}>
      <HeroSection
        onUnlockScroll={() => setScrollEnabled(true)}
        onNav={(target) => {
          if (target === 'portfolio') handleNavClick(portfolioRef)
          if (target === 'resume') handleNavClick(resumeRef)
          if (target === 'contact') handleNavClick(contactRef)
        }}
      />

      <section ref={portfolioRef} className={styles.section}>
        <h2>Portfolio Section</h2>
      </section>
      <section ref={resumeRef} className={styles.section}>
        <h2>Resume Section</h2>
      </section>
      <section ref={contactRef} className={styles.section}>
        <h2>Contact Section</h2>
      </section>
    </main>
  )
}
